import React, { useContext} from 'react'
import { DataContext } from '../../DataAsm';
import Formregister from './Formregister';

export default function Register() {
    const value = useContext(DataContext)
    const [users] = value.users
    const Register = details => {
        console.log(details);
        if(details.Fullname===""){
            alert("Vui lng in h v tn!")
        }else if(details.Username===""){
            alert("Vui lng in tn ng nhp!")
        }else if(details.Email===""){
            alert("Vui lng in email!")
        }else if(details.Password===""){
            alert("Vui lng in mt khu!")
        }else{
            users.forEach(gg =>{
                if(details.Username === gg.Username && details.Email === gg.Email){
                    alert(" c ti khon ny!")
                }
                if(details.Username !== gg.Username && details.Email !== gg.Email){
                    fetch(`https://json-server-hkhoa.herokuapp.com/users`,{
                        method: 'POST',
                        headers: {
                            'Accept':'application/json',
                            'Content-Type':'application/json'
                        },
                        body:JSON.stringify(details)
                    })
                    alert("ng k thnh cng!")
                    window.location.href="/login"
                }
            })
        }
    }

    return (
        <div>
            <Formregister Register={Register} />
        </div>
    );
}