import React from 'react';
import {Switch, Route} from 'react-router-dom';
import Showcatalog from './components/admin/catalog/Showcatalog';
import Createcatalog from './components/admin/catalog/themxoasua/Createcatalog';
import Inhd from './components/admin/order/Inhd';
import Showorder from './components/admin/order/Showorder';
import Showproduct from './components/admin/product/Showproduct';
import Createproduct from './components/admin/product/themxoasua/Createproduct';
import Editproduct from './components/admin/product/themxoasua/Editproduct';
import Bangthongke from './components/admin/thongke/Bangthongke';
import Bieudothongke from './components/admin/thongke/Bieudothongke';
import Showuser from './components/admin/user/Showuser';
import Edituser from './components/admin/user/themxoasua/Edituser';
import Cart from './components/cart/cart/Cart';
import Checkout from './components/cart/checkout/Checkout';
import Category from './components/category/Category';
import BaocaoASM from './components/contact/BaocaoASM';
import Contact from './components/contact/Contact';
import Home from './components/Home';
import Footer from './components/layout/footer/Footer';
import Header from './components/layout/header/Header';
import Productdetails from './components/product/productdetails/Productdetails';
import Productlike from './components/product/productlike/Productlike';
import Products from './components/product/products/Products';
import Forgotpass from './components/user/forgotpass/Forgotpass';
import Login from './components/user/login/Login';
import Profile from './components/user/profile/Profile';
import Register from './components/user/register/Register';
import Resetpass from './components/user/resetpass/Resetpass';
// import Slider from './components/layout/slider/Slider'
import FormTimHinh from './components/user/formtimhinh/FormTimHinh';

function App() {
    const dataUser = localStorage.getItem("dataUser");
    const dataUsers = JSON.parse(dataUser);
    return (
            <div className="App">
                <Header/>
                {/* <Slider/> */}
                <section>
                {(dataUsers) ? (
                    <Switch>
                        <Route exact path="/"><Home /></Route>
                        <Route exact path="products"><Products /></Route>
                        <Route exact path="product-details/:id"><Productdetails /></Route>
                        <Route exact path="productlike"><Productlike /></Route>
                        <Route exact path="cart"><Cart /></Route>
                        <Route exact path="checkout"><Checkout /></Route>
                        <Route exact path="login"><Home /></Route>
                        <Route exact path="register"><Home /></Route>
                        <Route exact path="profile/:id"><Profile /></Route>
                        <Route exact path="reset-password/:id"><Resetpass /></Route>
                        <Route exact path="forgot-password"><Home /></Route>
                        <Route exact path="contact"><Contact /></Route>
                        <Route exact path="category/:id"><Category /></Route>
                        <Route exact path="admin-catalogs"><Showcatalog /></Route>
                        <Route exact path="create-catalogs"><Createcatalog /></Route>
                        <Route exact path="admin-products"><Showproduct /></Route>
                        <Route exact path="create-products"><Createproduct /></Route>
                        <Route exact path="edit-products/:id"><Editproduct /></Route>
                        <Route exact path="admin-users"><Showuser /></Route>
                        <Route exact path="edit-users/:id"><Edituser /></Route>
                        <Route exact path="admin-orders"><Showorder /></Route>
                        <Route exact path="in-hoadon/:id"><Inhd /></Route>
                        <Route exact path="bangthongke"><Bangthongke /></Route>
                        <Route exact path="bieudothongke"><Bieudothongke /></Route>
                        <Route exact path="baocao"><BaocaoASM /></Route>
                        <Route exact path="timhinh"><FormTimHinh /></Route>
                        <Route path="*">Not found</Route>
                    </Switch>
                    ) : (
                    <Switch>
                        <Route exact path="/"><Home /></Route>
                        <Route exact path="products"><Products /></Route>
                        <Route exact path="product-details/:id"><Productdetails /></Route>
                        <Route exact path="productlike"><Productlike /></Route>
                        <Route exact path="cart"><Cart /></Route>
                        <Route exact path="checkout"><Checkout /></Route>
                        <Route exact path="login"><Login /></Route>
                        <Route exact path="register"><Register /></Route>
                        <Route exact path="profile/:id"><Login /></Route>
                        <Route exact path="reset-password/:id"><Login /></Route>
                        <Route exact path="forgot-password"><Forgotpass /></Route>
                        <Route exact path="contact"><Contact /></Route>
                        <Route exact path="category/:id"><Category /></Route>
                        <Route exact path="admin-products"><Login /></Route>
                        <Route exact path="create-products"><Login /></Route>
                        <Route exact path="edit-products/:id"><Login /></Route>
                        <Route exact path="admin-users"><Login /></Route>
                        <Route exact path="edit-users/:id"><Login /></Route>
                        <Route exact path="admin-orders"><Login /></Route>
                        <Route exact path="in-hoadon/:id"><Login /></Route>
                        <Route exact path="bangthongke"><Login /></Route>
                        <Route exact path="bieudothongke"><Login /></Route>
                        <Route exact path="baocao"><BaocaoASM /></Route>
                        <Route exact path="timhinh"><FormTimHinh /></Route>
                        <Route path="*">Not found</Route>
                    </Switch>
                )}
                </section>
                <Footer/>
            </div>
    );
}

export default App;
