import React, {useContext, useState} from 'react';
import { DataContext } from '../../DataAsm';

export default function Showuser() {
    const value = useContext(DataContext)
    const [users] = value.users

    const [currentPage, setCurrentPage] = useState(1)
    const [postPerPage] = useState(4)
    const totalPost = users.length
    const pageNumber = []
    for(let i = 1; i<= Math.ceil(totalPost / postPerPage); i++){
        pageNumber.push(i)
    }

    const indexOfLastPost = currentPage * postPerPage
    const indexOfFirstPost = indexOfLastPost - postPerPage

    const paginate = pageNumber => setCurrentPage(pageNumber)

    const xoauser = (id) => {
        if (window.confirm("Bn thc s mun xa ti khon ny?")) {
            fetch(`https://json-server-hkhoa.herokuapp.com/users/${id}`,{
                method: 'DELETE',
                headers: {
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify()
            })
            .then(res=>{
                return res.json();
            })
            .then((resp) => {
                window.location.href="/admin-users"
            })
            .catch((error) => console.log(error));
        }
    };

    return (
        <div id="content"> 
            <section className="shop-page padding-top-100 padding-bottom-100">
            <div className="container">
                <div className="row"> 
                <div className="col-sm-3">
                    <div className="shop-sidebar"> 
                    <h5 className="shop-tittle margin-bottom-30">danh mc</h5>
                    <ul className="shop-cate">
                        <li><a href="/admin-catalogs">Qun tr danh mc</a></li>
                        <li><a href="/admin-products">Qun tr sn phm</a></li>
                        <li><a href="/admin-users">Qun tr ti khon</a></li>
                        <li><a href="/admin-orders">Qun tr n hng</a></li>
                        <li><a href="/bangthongke">Thng k</a></li>
                    </ul>
                    <div className="side-bnr margin-top-50"> <img className="img-responsive" src="images/sidebar-bnr.jpg" alt="" />
                        <div className="position-center-center"> <span className="price">1,999,000 VND</span>
                        <div className="bnr-text" style={{fontSize:"28px"}}>Trng
                            nng bng
                            vi
                            phong cch</div>
                        </div>
                    </div>
                    </div>
                </div>
                <div className="col-sm-9">
                <div className="item-display">
                    <div className="row">
                        <div className="col-xs-6"> <span className="product-num"> Qun tr ti khon</span> </div>
                        <div className="col-xs-6">
                        <div className="pull-right"> 
                            <a href="#." className="grid-style"><i className="icon-grid" /></a> <a href="#." className="list-style"><i className="icon-list" /></a> </div>
                        </div>
                    </div>
                </div>
                    <div className="papular-block row single-img-demos"> 
                        <table className="table">
                            <thead>
                            <tr>
                                <th scope="col">ID</th>
                                <th scope="col">H Tn</th>
                                <th scope="col">Tn ti khon</th>
                                <th scope="col">Email</th>
                                <th scope="col">Quyn</th>
                                <th scope="col">Ty chnh</th>
                            </tr>
                            </thead>
                            <tbody>
                                {
                                    users.slice(indexOfFirstPost, indexOfLastPost).map(user=>(
                                        <tr key={user.id}>
                                            <th scope="row">{user.id}</th>
                                            <td>{user.Fullname}</td>
                                            <td>{user.Username}</td>
                                            <td>{user.Email}</td>
                                            {
                                                (user.role)===0 ?(
                                                    <td>Admin</td>
                                                ): (
                                                    <td>Khch hng</td>
                                                )
                                            }
                                            {
                                                (user.role)===0 ?(
                                                    <td><a href={`/edit-users/${user.id}`}><button className="suaxoa" style={{borderRadius:"5px"}}>Sa</button></a></td>
                                                ): (
                                                    <td><a href={`/edit-users/${user.id}`}><button className="suaxoa" style={{borderRadius:"5px"}}>Sa</button></a> <button className="suaxoa" onClick={()=>xoauser(user.id)} style={{borderRadius:"5px"}}>Xa</button></td>
                                                )
                                            }
                                            
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </table>
                    </div>
                    <ul className="pagination">
                        {
                            pageNumber.map(number=>(
                                <li key={number}><button onClick={()=>paginate(number)}>{number}</button></li>
                            ))
                        }
                    </ul>
                </div>
                </div>
            </div>
            </section>
        </div>
    );
}
