import React, { useContext } from 'react';
import { DataContext } from '../../DataAsm';

export default function Bangthongke() {
    const value = useContext(DataContext)
    const [products] = value.products

    return (
        <div id="content"> 
            <section className="shop-page padding-top-100 padding-bottom-100">
            <div className="container">
                <div className="row"> 
                <div className="col-sm-3">
                    <div className="shop-sidebar"> 
                    <h5 className="shop-tittle margin-bottom-30">danh mc</h5>
                    <ul className="shop-cate">
                        <li><a href="/admin-catalogs">Qun tr danh mc</a></li>
                        <li><a href="/admin-products">Qun tr sn phm</a></li>
                        <li><a href="/admin-users">Qun tr ti khon</a></li>
                        <li><a href="/admin-orders">Qun tr n hng</a></li>
                        <li><a href="/bangthongke">Thng k</a></li>
                    </ul>
                    <div className="side-bnr margin-top-50"> <img className="img-responsive" src="images/sidebar-bnr.jpg" alt="" />
                        <div className="position-center-center"> <span className="price">1,999,000 VND</span>
                        <div className="bnr-text" style={{fontSize:"28px"}}>Trng
                            nng bng
                            vi
                            phong cch</div>
                        </div>
                    </div>
                    </div>
                </div>
                <div className="col-sm-9">
                <div className="item-display">
                    <div className="row">
                        <div className="col-xs-6"> <span className="product-num"> Bng thng k</span> </div>
                        <div className="col-xs-6">
                        <div className="pull-right"> 
                            <a href="#." className="grid-style"><i className="icon-grid" /></a> <a href="#." className="list-style"><i className="icon-list" /></a> </div>
                        </div>
                    </div>
                </div>
                    <div className="papular-block row single-img-demos"> 
                        <h5 style={{textAlign: 'center'}}>Thng k Bn</h5>
                        <div className="col-xs-1 col-sm-1 col-md-1 col-lg-1" />
                        <div className="col-xs-10 col-sm-10 col-md-10 col-lg-10">
                        <table className="table table-hover" border="0.5">
                            <thead>
                            <tr>
                                <th>Mc sn phm</th>
                                <th>S lng</th>
                                <th>Gi cao nht</th>
                                <th>Gi thp nht</th>
                            </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>Bn</td>  
                                    <td>{products.length}</td>
                                {
                                    products.sort((a,b)=>(b.price-a.price)).slice(0,1).map(pos=>(
                                
                                    <td key={pos.id}>{pos.price}</td>
                                
                                    ))
                                }
                                {
                                    products.sort((a,b)=>(a.price-b.price)).slice(0,1).map(pos=>(
                                
                                    <td key={pos.id}>{pos.price}</td>
                                
                                    ))
                                }
                                </tr>
                            </tbody>
                        </table>
                        <a href="/bieudothongke" className="btn btn-info" style={{textAlign:"center", marginLeft:"230px"}}>Xem biu </a>
                        </div>
                    </div>
                    {/* <ul className="pagination">
                        {
                            pageNumber.map(number=>(
                                <li key={number}><button onClick={()=>paginate(number)}>{number}</button></li>
                            ))
                        }
                    </ul> */}
                </div>
                </div>
            </div>
            </section>
        </div>
    );
}
