import React, { useEffect, useState} from 'react';
import { useDispatch, useSelector } from "react-redux";
import { addProduct, loadCatalogs } from '../../../../actions/actions';

export default function Createproduct(props) {
    let dispatch = useDispatch();
    const {catalogs} = useSelector((states) => states.data2);
    useEffect(() => {
        dispatch(loadCatalogs());
    }, [dispatch]);
    const [details, setDetails] = useState({
        title: "", 
        images: "", 
        description: "",
        price: "",
        idcate: "",
        namecates: "",
        content: "",
        count: 1,
        like: "",
        like2: "thch",
        view: 1,
        view2: 1,
        hot: 0
    });

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!details.title || !details.images || !details.description || !details.price) {
          alert("Vui lng nhp y !")
        } else {
            catalogs.forEach(gg =>{
                if(details.namecates === gg.namecate){
                    const offff = ({
                        title: details.title, 
                        images: details.images.replace( "C:\\fakepath\\",""), 
                        description: details.description,
                        price: details.price,
                        idcate: gg.id,
                        namecates: details.namecates,
                        content: "",
                        count: 1,
                        like: "",
                        like2: "thch",
                        view: 1,
                        view2: 1,
                        hot: 0
                    })
                    dispatch(addProduct(offff));
                }
                
            })
        }alert("Thm Thnh Cng.")
    };

    return (
        <div id="content">
            <section className="shop-page padding-top-100 padding-bottom-100">
                <div className="container">
                    <h2 style={{textAlign:"center"}}>Thm sn phm</h2> <br/>
                    <form className="form" onSubmit={handleSubmit} style={{marginLeft:"270px"}}>
                        <div className="container">
                        <label htmlFor="Username"><b>Tn sn phm:</b></label><br />
                        <input className="input" type="text" placeholder="Tn sn phm" name="title" id="title" onChange={e=>setDetails({...details, title: e.target.value})} value={details.title}/><br />
                        <label htmlFor="psw"><b>Hnh nh:</b></label><br />
                        <input className="input" type="file" placeholder="Password" name="images" id="images" onChange={e=>setDetails({...details, images: e.target.value})} value={details.images} /><br />
                        <label htmlFor="Username"><b>M t sn phm:</b></label><br />
                        <input className="input" type="text" placeholder="M t sn phm" name="description" id="description" onChange={e=>setDetails({...details, description: e.target.value})} value={details.description}/><br />
                        <label htmlFor="Username"><b>Gi sn phm:</b></label><br />
                        <input className="input" type="number" placeholder="Gi sn phm" name="price" id="price" onChange={e=>setDetails({...details, price: e.target.value})} value={details.price} /><br />
                        <label htmlFor="Username"><b>Loi sn phm:</b></label><br />
                        <select name="namecates" id="namecates"  className="input" onChange={e=>setDetails({...details, namecates: e.target.value})}>
                        {
                            catalogs.map(catalog=>(
                                <option key={catalog.id} value={ catalog.namecate} >{catalog.namecate}</option>
                            ))
                        }
                        </select>
                        <br />
                        <button type="submit"  className="button">Thm</button>
                        </div>
                    </form>
                </div>
            </section>
            
        </div>
    );
}