import React,{useContext, useState, useEffect} from 'react'
import { DataContext } from '../../DataAsm';

export default function Checkout() {
    const value = useContext(DataContext)
    const [cart, setCart] = value.cart;
    const [total, setTotal] = useState(0)
    const [quantity, setTQty] = useState(0)
    const dataUser = localStorage.getItem("dataUser")
    const dataUsers = JSON.parse(dataUser)

    useEffect(() =>{
        const getTotal = () => {
            const res = cart.reduce((prev, item) => {
                return prev + (item.price * item.count)
            },0)
            setTotal(res)
        }
        getTotal()

        const getQty = () => {
            const res = cart.reduce((prevs, items) => {
                return prevs + (items.count)
            },0)
            setTQty(res)
        }
        getQty()

    },[cart])

    const [details, setDetails] = useState({
        Fullname:"",
        Quequan:"",
        Diachihientai:"",
        Sodienthoai:"",
        Email:"",
        Total:""
    })

    const handleSubmit = (e) => {
        e.preventDefault();
        if(dataUsers){
            if (!details.Quequan || !details.Diachihientai || !details.Sodienthoai) {
                alert("Vui lng nhp y !")
            } else {        
                if(total===0){
                    alert("Bn cha mua hng! Mi bn mua hng ri quay li y!")
                }else{
                    const offff = ({
                        Fullname: dataUsers.Fullname,
                        Quequan: details.Quequan,
                        Diachihientai: details.Diachihientai,
                        Sodienthoai: details.Sodienthoai,
                        Email: dataUsers.Email,
                        Total: total
                    })
                    fetch(`https://json-server-hkhoa.herokuapp.com/orders`,{
                        method: 'POST',
                        headers: {
                            'Accept':'application/json',
                            'Content-Type':'application/json'
                        },
                        body:JSON.stringify(offff)
                    })
                    .then(res=>{
                        return res.json();
                    })
                    .then((resp) => {       
                        cart.splice(cart)
                        setCart([...cart])                               
                        window.location.href="/"     
                    })
                    .catch((error) => console.log(error));
                }
            }
        }else{
            if (!details.Fullname || !details.Quequan || !details.Diachihientai || !details.Sodienthoai || !details.Email) {
                alert("Vui lng nhp y !")
            } else {
                if(total===0){
                    alert("Bn cha mua hng! Mi bn mua hng ri quay li y!")
                }else{
                    const offff = ({
                        UserName: details.UserName,
                        Quequan: details.Quequan,
                        Diachihientai: details.Diachihientai,
                        Sodienthoai: details.Sodienthoai,
                        Email: details.Email,
                        Total: total
                    })
                    fetch(`https://json-server-hkhoa.herokuapp.com/orders`,{
                        method: 'POST',
                        headers: {
                            'Accept':'application/json',
                            'Content-Type':'application/json'
                        },
                        body:JSON.stringify(offff)
                    })
                    .then(res=>{
                        return res.json();
                    })
                    .then((resp) => {    
                        cart.splice(cart)
                        setCart([...cart])             
                        window.location.href="/"
                    })
                    .catch((error) => console.log(error));
                }
            }
        }
    };

    return (
        <div id="content"> 
            <section className="chart-page padding-top-100 padding-bottom-100">
            <div className="container"> 
                <div className="shopping-cart"> 
                <div className="cart-ship-info">
                    <div className="row"> 
                    <div className="col-sm-7">
                        <h6>CHI TIT HA N</h6>
                        {
                            (dataUsers) ? (
                                <form onSubmit={handleSubmit}>
                                    <ul className="row">
                                        <li className="col-md-6">
                                        <label> H V TN
                                            <input type="text" name="last-name" onChange={e=>setDetails({...details, Fullname: e.target.value})} value={dataUsers.Fullname} disabled/>
                                        </label>
                                        </li>
                                        <li className="col-md-6"> 
                                        <label>QU QUN
                                            <input type="text" name="company" onChange={e=>setDetails({...details, Quequan: e.target.value})} value={details.Quequan} placeholder="qu qun" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6"> 
                                        <label>*A CH HIN TI
                                            <input type="text" name="address" onChange={e=>setDetails({...details, Diachihientai: e.target.value})} value={details.Diachihientai} placeholder="a ch hin ti" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <label>*S IN THOI
                                            <input type="Number" name="contry-state" onChange={e=>setDetails({...details, Sodienthoai: e.target.value})} value={details.Sodienthoai} placeholder="s in thoi" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <label>*A CH EMAIL
                                            <input type="text" name="contry-state" onChange={e=>setDetails({...details, Email: e.target.value})} value={dataUsers.Email} disabled/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <label> *TNG S TIN
                                            <input type="Number" name="postal-code" onChange={e=>setDetails({...details, Total: e.target.value})} value={`${total}`} disabled/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <button type="submit" className="btn">lu thng tin</button>
                                        </li>
                                    </ul>
                                </form>
                            ):(
                                <form onSubmit={handleSubmit}>
                                    <ul className="row">
                                        <li className="col-md-6">
                                        <label> H V TN
                                            <input type="text" name="last-name" onChange={e=>setDetails({...details, Fullname: e.target.value})} value={details.Fullname} placeholder="h v tn" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6"> 
                                        <label>QU QUN
                                            <input type="text" name="company" onChange={e=>setDetails({...details, Quequan: e.target.value})} value={details.Quequan} placeholder="qu qun" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6"> 
                                        <label>*A CH HIN TI
                                            <input type="text" name="address" onChange={e=>setDetails({...details, Diachihientai: e.target.value})} value={details.Diachihientai} placeholder="a ch hin ti" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <label>*S IN THOI
                                            <input type="text" name="contry-state" onChange={e=>setDetails({...details, Sodienthoai: e.target.value})} value={details.Sodienthoai} placeholder="s in thoi" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <label>*A CH EMAIL
                                            <input type="text" name="contry-state" onChange={e=>setDetails({...details, Email: e.target.value})} value={details.Email} placeholder="email" required/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <label> *TNG S TIN
                                            <input type="text" name="postal-code" onChange={e=>setDetails({...details, Total: e.target.value})} value={`${total}`} disabled/>
                                        </label>
                                        </li>
                                        <li className="col-md-6">
                                        <button type="submit" className="btn">lu thng tin</button>
                                        </li>
                                    </ul>
                                </form>
                            )
                        }
                        
                        
                    </div>
                    <div className="col-sm-5">
                        <h6>N HNG CA BN</h6>
                        <div className="order-place">
                            <div className="order-detail">
                            {
                                cart.map(product =>(                                
                                    <p key={product.id}>{product.title} x {product.count} <span>{(product.price*product.count).toLocaleString("en-GB")} VND</span></p>
                                ))
                            }
                                <p className="all-total">Tng s lng <span>{quantity} x sn phm</span></p>
                                <p className="all-total">Tng gi tin <span>{total} VND</span></p>
                            </div>
                        <div className="pay-meth">
                            <ul>
                            <li>
                                <div className="radio">
                                <input type="radio" name="radio1" id="radio1" defaultValue="option1" defaultChecked />
                                <label htmlFor="radio1"> THANH TON QUA TH NGN HNG / VISA </label>
                                </div>
                            </li>
                            <li>
                                <div className="radio">
                                <input type="radio" name="radio1" id="radio2" defaultValue="option2" />
                                <label htmlFor="radio2"> THANH TON BNG TIN MT</label>
                                </div>
                            </li>
                            <li>
                                <div className="radio">
                                <input type="radio" name="radio1" id="radio4" defaultValue="option4" />
                                <label htmlFor="radio4"> THANH TON QUA PAYPAL </label>
                                </div>
                            </li>
                            </ul> 
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
                </div>
            </div>
            </section>
        </div>
    );
}

